.navbar {
  height: 50px;
  border-bottom: 0.5px solid rgb(231, 228, 228);
  align-items: center;
  font-size: 14px;
  color: #555;

  .wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;

    .search {
      display: flex;
      align-items: center;
      border: 1px solid lightgray;
      padding: 3px;
      cursor: pointer;

      input {
        border: none;
        outline: none;
        background-color: transparent;
        &::placeholder {
          font-size: 12px;
        }
      }
    }

    .items {
      display: flex;
      align-items: center;
      gap: 15px;

      .item {
        display: flex;
        align-items: center;
        position: relative;

        cursor: pointer;

        .icon {
          font-size: 20px;
        }

        .counter {
          width: 15px;
          height: 15px;
          background-color: red;
          color: white;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 10px;
          font-weight: bold;

          position: absolute;
          top: -5px;
          right: -5px;
        }

        .avatar {
          width: 30px;
          height: 30px;
          border-radius: 50%;
          object-fit: cover;
        }
      }
    }
  }
}
